import { Auth } from 'aws-amplify'
import customerAccessTokenCreateWithMultipass from './customerAccessTokenCreateWithMultipass'
import { getUserSession } from '../utils'

const multipassTokenFetchURL =
  process.env.GATSBY_COGNITO_SHOPIFY_MULTIPASS_TOKEN_ENDPOINT

const fetchCustomerAccessToken = async client => {
  if (!Auth.user) return null
  const userSession = await getUserSession()
  const headers = new Headers()
  headers.append('Content-Type', 'application/json')
  headers.append('Authorization', userSession.idToken.jwtToken)

  const multipassFetch = await fetch(multipassTokenFetchURL, {
    method: 'GET',
    headers,
  })
  const multipassToken = await multipassFetch.json()

  const accessTokenResult = await customerAccessTokenCreateWithMultipass(
    client,
    {
      multipassToken,
    },
  )

  const {
    data: {
      customerAccessTokenCreateWithMultipass: { customerAccessToken },
    },
  } = accessTokenResult

  return customerAccessToken
}

export default fetchCustomerAccessToken
