import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'

import ScaledImage from '../../components/ScaledImage'

const StyledAuthWrapper = styled.section`
  display: flex;
  flex-direction: column;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: row;
  }
`

const ImageWrapper = styled.div`
  position: relative;
  box-sizing: border-box;
  height: ${props => (props.largeImage ? '587px' : '310px')};
  flex-shrink: 0;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    top: 0;
    width: ${props => (props.largeImage ? desktopVw(880) : '50%')};
    height: ${props =>
      `calc(100vh - ${desktopVw(props.theme.announcementBar.desktop.height)})`};
    margin: 0;
    margin-top: -${props => desktopVw(props.theme.header.desktop.height)};
  }
`

const ImageTextWrapper = styled.div`
  text-align: center;
  justify-self: center;
`

const ImageText = styled.div`
  white-space: pre-wrap;
  text-transform: uppercase;
  color: ${props => props.theme.colors.red};
  font-family: ${props => props.theme.fonts.secondary};
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  font-size: 80px;
  line-height: 70px;
  letter-spacing: -2px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(200)};
    line-height: ${desktopVw(169)};
    letter-spacing: -${desktopVw(2)};
  }
`

const ImageTextAlt = styled.div`
  position: absolute;
  top: 124px;
  left: 20px;
  right: 20px;
  bottom: 0;
  font-size: 80px;

  letter-spacing: -2px;
  text-transform: uppercase;
  color: ${props => props.theme.colors.red};
  font-family: ${props => props.theme.fonts.secondary};

  div {
    margin-bottom: 15px;
    text-align: left;

    &:nth-child(2) {
      text-align: right;
    }

    &:nth-child(3) {
      margin-left: 20px;
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    top: 50%;
    transform: translateY(-50%);
    left: ${desktopVw(80)};
    right: ${desktopVw(200)};
    font-size: ${desktopVw(130)};
    letter-spacing: -${desktopVw(2)};

    div {
      margin-bottom: ${desktopVw(20)};

      &:nth-child(3) {
        margin-top: -${desktopVw(10)};
        margin-left: ${desktopVw(40)};
      }
    }
  }
`

const FormContainer = styled.div`
  padding: 0 20px;
  align-self: center;
  position: relative;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    flex-direction: column;
    justify-content: center;

    width: ${desktopVw(400)};

    margin: auto;
    padding: 0 ${desktopVw(80)};
    height: ${({ theme }) =>
      `calc(100vh - ${desktopVw(
        theme.announcementBar.desktop.height + theme.header.desktop.height,
      )})`};
  }
`

const FormWrapper = styled.div`
  margin: 18px 0 0;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0;
    overflow-y: scroll;
    &::-webkit-scrollbar {
      display: none;
    }
  }
`

const AuthWrapper = ({ children, image, imageText, stage, largeImage }) => {
  return (
    <StyledAuthWrapper>
      <ImageWrapper
        largeImage={largeImage}
        className='authwrapper__image-wrapper'
      >
        <ScaledImage
          style={{ height: '100%', width: '100%' }}
          fluid={image.fluid}
        />
        {imageText && stage === 0 && (
          <ImageTextWrapper>
            <ImageText>{imageText}</ImageText>
          </ImageTextWrapper>
        )}
        {imageText && stage === 1 && (
          <ImageTextWrapper>
            <ImageTextAlt>
              <div>Share</div>
              <div>The</div>
              <div>Word</div>
            </ImageTextAlt>
          </ImageTextWrapper>
        )}
      </ImageWrapper>
      <FormContainer className='authwrapper__form-container'>
        <FormWrapper className='authwrapper__form-wrapper'>
          {children}
        </FormWrapper>
      </FormContainer>
    </StyledAuthWrapper>
  )
}

export default AuthWrapper
