import { Auth } from 'aws-amplify'
import { useDispatch } from 'react-redux'
import { useContext } from 'react'
import { saveState, setCookie, setUser } from '../utils'
import { logInUser } from '../actions'
import fetchCustomerAccessToken from '../shopify/fetchCustomerAccessToken'
import { StoreContext } from '../contexts'
import { ONE_YEAR_IN_MINUTES } from '../utils/constants'
import { captureException } from '../components/Sentry/utils'

const saveCustomerAccessToken = saveState('customerAccessToken')

function useLoginUser() {
  const dispatch = useDispatch()
  const stores = useContext(StoreContext)

  const loginUser = async (username, password) => {
    await Auth.signIn(username, password)
    const user = await Auth.currentAuthenticatedUser()

    if (!user?.signInUserSession?.idToken?.payload?.['custom:customer_id']) {
      captureException(
        `Customer id not present for ${user?.signInUserSession?.idToken?.payload?.email}`,
        user,
        'AWS_CONGNITO',
      )
    }

    const userInfo = {
      ...user.attributes,
    }
    setUser(userInfo)
    setCookie('jwt_token', user?.signInUserSession?.idToken?.jwtToken, 720)
    setCookie(
      'refresh_token',
      user?.signInUserSession?.refreshToken?.token,
      ONE_YEAR_IN_MINUTES,
    )
    dispatch(logInUser(userInfo))
    const customerAccessToken = await fetchCustomerAccessToken(
      stores.lastbrand.client,
    )

    saveCustomerAccessToken(customerAccessToken)
  }

  return { loginUser }
}

export default useLoginUser
